
.box[data-v-87e066ae] {
	margin: 0 auto;
	width: 500px;
}
.container[data-v-87e066ae] {
	padding: 10px;
	/* border: 1px solid #333; */
	border-bottom: 1px solid #333;
	margin: 0 auto;
}
.checkbox[data-v-87e066ae] {
    position: relative;
    display: block;
}
.checkbox input[data-v-87e066ae] {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
}
.checkbox label[data-v-87e066ae] {
    position: relative;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    line-height: 30px;
    padding-left: 26px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.checkbox label[data-v-87e066ae]:before {
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -10px;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 14px;
    content: '';
    border: 1px solid #333;
    display: block;
    font-size: 30px;
    color: #8c8;
}
.checkbox input[type="checkbox"]:checked + label[data-v-87e066ae]:before {
    background-image: url("data:image/png;base64,");
    background-size: cover;
    border-color: #00b7ee;
}
.switch[data-v-87e066ae] {
    position: relative;
    display: inline-block;
    min-height: 24px;
    margin-top: 6px;
}
.switch input[type="checkbox"][data-v-87e066ae] {
    box-sizing: border-box;
    left: 4px;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 3px;
}
.switch input[type="checkbox"] + label[data-v-87e066ae] {
    background-color: #e0e0e0;
    border-radius: 24px;
    cursor: pointer;
    display: inline-block;
    height: 24px;
    position: relative;
    text-indent: -9999px;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 48px;
}
.switch input[type="checkbox"] + label[data-v-87e066ae]:after {
    background-color: #fff;
    border-radius: 20px;
    content: " ";
    height: 20px;
    left: 2px;
    position: absolute;
    top: 2px;
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    width: 20px;
}
.switch input[type="checkbox"]:checked + label[data-v-87e066ae] {
    background-color: #8c8;
}
.switch input[type="checkbox"]:checked + label[data-v-87e066ae]:after {
    -webkit-transform: translateX(24px);
    transform: translateX(24px);
}
.radio[data-v-87e066ae] {
    position: relative;
}
.radio input[data-v-87e066ae] {
    position: absolute;
    visibility: hidden;
}
.radio label[data-v-87e066ae] {
    position: relative;
    display: inline-block;
    padding-left: 24px;
    height: 20px;
    line-height: 20px;
}
.radio label[data-v-87e066ae]:before,
.radio label[data-v-87e066ae]:after {
    position: absolute;
    top: 50%;
    left: 0;
    content: '';
    box-sizing: border-box;
    border-radius: 50%;
}
.radio label[data-v-87e066ae]:before {
    height: 20px;
    width: 20px;
    margin-top: -10px;
    border: 1px solid #000;
}
.radio input[type="radio"]:checked + label[data-v-87e066ae]:before {
    border-color: #8c8;
}
.radio input[type="radio"]:checked + label[data-v-87e066ae]:after {
    width: 14px;
    height: 14px;
    margin-top: -7px;
    margin-left: 3px;
    background-color: #8c8;
}




